les';\n@import '~quill/dist/quill.bubble.css';\n\n.ql-container {\n    font-size: 1.1rem;\n    line-height: 2;\n    word-wrap: normal;\n    font-family: $font-family-serif;\n}\n\n.ql-editor {\n    font-family: $font-family-serif;\n    font-size: 1.1rem !important;\n    line-height: 2 !important;\n    padding-left: 0 !important;\n    padding-right: 0 !important;\n    overflow-y: visible !important;\n    min-width: 100%;\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    -webkit-box-orient: vertical;\n    -webkit-box-direction: normal;\n    -ms-flex-direction: column;\n    flex-direction: column;\n}\n\n.ql-editor p {\n    margin: 1.5em 0 0 0 !important;\n}\n\n.ql-editor a {\n    text-decoration: underline;\n}\n\n.ql-editor h1,\nh2,\nh3 {\n    margin: 1.5em 0 0 0 !important;\n}\n\n.ql-editor blockquote {\n    margin: 2em 0 1em 0 !important;\n    font-style: italic;\n    font-size: 28px;\n    border: none !important;\n    color: $gray-500;\n    padding-left: 1.5em !important;\n    line-height: 1.5;\n}\n\ndiv.embedded_image {\n    margin-top: 2em;\n}\n\ndiv.embedded_image > img {\n    width: 100%;\n    height: auto;\n    display: block;\n}\n\ndiv.embedded_image > p {\n    text-align: center;\n    color: $gray-500;\n    margin-top: 0.5em;\n    font-size: 0.9rem;\n    font-family: $font-family-sans-serif, sans-serif;\n}\n\ndiv.ql-embed-content {\n    // This is here because we have no way to control the dimensions\n    // of the content being embedded, so hide any overlap to avoid\n    // breaking either the editor or the screen.\n    overflow: hidden;\n}\n\ndiv.embedded_image:hover img,\ndiv.ql-embed-content:hover {\n    cursor: pointer !important;\n    box-shadow: 0 0 0 3px $green;\n}\n\ndiv.embedded_image[data-layout='wide'] img {\n    max-width: 1024px;\n    margin: 0 auto 30px;\n}\n\ndiv.embedded_image[data-layout='wide'] {\n    width: 100vw;\n    position: relative;\n    left: 50%;\n    margin-left: -50vw;\n}\n\n.ql-container hr {\n    border: none;\n    margin: 2em 0 3em 0;\n    letter-spacing: 1em;\n    text-align: center;\n}\n\n.ql-container hr:before {\n    content: '...';\n}\n\n.ql-editor pre.ql-syntax {\n    border-radius: $border-radius !important;\n    padding: 1em !important;\n    margin-top: 2em !important;\n}\n\n.ql-editor.ql-blank::before {\n    left: 0 !important;\n}\n\n.btn-circle {\n    width: 40px;\n    height: 40px;\n    padding: 6px 0;\n    border-radius: 9999px;\n    text-align: center;\n    line-height: 1.42857;\n}\n\n.sidebar-controls {\n    margin-top: -8px;\n    top: 0;\n    display: none;\n    position: absolute;\n    z-index: 10;\n    left: -60px;\n}\n\n.sidebar-controls button:hover {\n    background-color: transparent;\n}\n\n.sidebar-controls button:focus {\n    -webkit-box-shadow: none;\n    -moz-box-shadow: none;\n    box-shadow: none;\n    outline: none;\n}\n\n.sidebar-controls.active .controls {\n    display: inline-block !important;\n}\n\n.navbar div.btn-group {\n    flex: auto;\n}\n\n.navbar div.btn-group button {\n    border-radius: 0;\n}\n\ndiv.ql-editor.ql-blank::before {\n    margin-top: 26.4px !important;\n}\n\ndiv.embedded_image[data-layout='wide'] {\n    width: 100vw;\n    position: relative;\n    left: 50%;\n    margin-left: -50vw;\n}\n\n@media screen and (max-width: 1024px) {\n    .embedded_image[data-layout='wide'] img {\n        max-width: 100%;\n    }\n}\n\n@media (max-width: 1200px) {\n    .sidebar-controls {\n        display: none !important;\n    }\n}\n","@import url('https://fonts.googleapis.com/css?family=Karla&family=Merriweather&display=swap');\n\n$font-family-sans-serif: 'Karla', sans-serif;\n$font-family-serif: 'Merriweather', serif;\n\n$enable-responsive-font-sizes: true;\n\n$line-height-base: 1.6;\n\n$font-weight-light: 300;\n$font-weight-normal: 400;\n$font-weight-bold: 700;\n\n$white: #ffffff;\n$gray-100: #f7fafc;\n$gray-200: #edf2f7;\n$gray-300: #e2e8f0;\n$gray-400: #cbd5e0;\n$gray-500: #a0aec0;\n$gray-600: #718096;\n$gray-700: #4a5568;\n$gra